<template>
  <div>
    <div class="adiv">
      <van-icon name="arrow-left" class="icon" @click="$router.back()" />
      <h1>忘记密码</h1>
    </div>
     <van-form @submit="onSubmit" >
      <van-field
        v-model="username1"
        name="username"
        label="用户名"
        placeholder="用户名"
      />

      <van-field
        v-model="password1"
        type="password"
        name="password"
        label="密码"
        placeholder="密码"
      />
      <van-field
        v-model="yzm"
        type="number"
        name="vercode"
        label="验证码"
        placeholder="验证码"
      />
      <van-tag type="primary" round :color="myColor" class="yzm" @click="yzmfn"
        >获取验证码</van-tag
      >
      <van-button
        block
        type="info"
        native-type="submit"
        :color="myColor"
        class="btn"
        >提交</van-button
      >
     
    </van-form>
  </div>
</template>

<script>
import {getforgetAPI,getyzm} from "@/api/home"
export default {
    name:"forget",
    data() {
        return {
           username1: "",
      password1: "",
      yzm: "", 
        }
    },
    methods: {
         yzmfn() {
          getyzm({mobile:this.username1,type:"login"}).then((result) => {
           
           this.yzm=result.data.data 
          this.$toast("验证码是" +result.data.data);

          }).catch((err) => {
            
          });
    },
    onSubmit(values) {
     getforgetAPI(values).then((result) => {
    
        this.$toast(result.data.msg);
        this.$notify({ type: "danger", message:result.data.msg })
        if(result.data.msg=="密码重置成功"){

              this.$router.push('/login')
        }
     }).catch((err) => {
      console.log('err: ', err);
      
     });
      
    },
    },

}
</script>

<style lang="scss" scoped>
.adiv {
  width: 100vw;
  height: 50px;
  background-color: $mycolor;
  display: flex;
  align-items: center;

  color: aliceblue;
  font-size: 14px;
  margin-bottom: 50px;
}
h1 {
  margin: 0 auto;
  font-size: 16px;
}
.icon {
  font-size: 32px;
}
.yzm {
  float: right;
  margin: 20px;
}
</style>

